"use client"

import { useState, useEffect } from "react"
import { useParams, useRouter } from "next/navigation"
import Link from "next/link"
import { useContract } from "@/context/contract-context"
import { useSubgraph } from "@/context/subgraph-context"
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Skeleton } from "@/components/ui/skeleton"
import { Alert, AlertDescription } from "@/components/ui/alert"
import { ArrowLeft, CalendarClock, Clock, Users, AlertCircle, Share2 } from "lucide-react"
import { formatDate } from "@/lib/utils"
import { toast, Toaster } from "sonner"
import CampaignCountdown from "./_components/campaign-countdown"
import OverviewTab from "./_components/overview-tab"
import RegisterTab from "./_components/register-tab"
import VoteTab from "./_components/vote-tab"
import ResultsTab from "./_components/results-tab"

export default function CampaignDetailsPage() {
  const params = useParams()
  const router = useRouter()
  const campaignId = params.id
  const { contract, address, isConnected, connectWallet } = useContract()
  const { events, loading: subgraphLoading, refetchEvents } = useSubgraph()

  // State variables
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [campaignInfo, setCampaignInfo] = useState(null)
  const [proposals, setProposals] = useState([])
  const [candidates, setCandidates] = useState([])
  const [userRegistered, setUserRegistered] = useState(false)
  const [userVoted, setUserVoted] = useState(false)
  const [isOwner, setIsOwner] = useState(false)
  const [activeTab, setActiveTab] = useState("overview")

  // Time-based states
  const [isBeforeStart, setIsBeforeStart] = useState(false)
  const [isDuringVoting, setIsDuringVoting] = useState(false)
  const [isAfterEnd, setIsAfterEnd] = useState(false)

  // Fetch campaign data from subgraph and contract
  useEffect(() => {
    const fetchCampaignData = async () => {
      if (!campaignId) return

      try {
        setLoading(true)
        setError(null)

        toast.loading("Loading campaign details...", { id: "campaign-loading" })

        // Find campaign in subgraph data
        const campaignCreatedEvent = events.campaignsCreated.find((event) => event.campaignId === campaignId)

        if (!campaignCreatedEvent && !subgraphLoading) {
          setError("Campaign not found")
          setLoading(false)
          toast.error("Campaign not found", { id: "campaign-loading" })
          return
        }

        // If contract is available, fetch additional data
        if (contract) {
          // Fetch basic campaign info
          const basicInfo = await contract.getCampaignBasicInfo(campaignId)
          const timeInfo = await contract.getCampaignTimeInfo(campaignId)
          const stats = await contract.getCampaignStats(campaignId)

          // Check time-based states
          const now = Math.floor(Date.now() / 1000)
          const startTime = Number(timeInfo.startTime)
          const endTime = Number(timeInfo.endTime)

          setIsBeforeStart(now < startTime)
          setIsDuringVoting(now >= startTime && now < endTime)
          setIsAfterEnd(now >= endTime)

          // Check if user is owner
          if (isConnected && address) {
            const isCreator = basicInfo.creator.toLowerCase() === address.toLowerCase()
            setIsOwner(isCreator)

            // Check if user is registered
            const regType = await contract.getUserRegistrationType(campaignId, address)
            setUserRegistered(Number(regType) > 0)

            // Check if user has voted
            const voted = await contract.hasUserVoted(campaignId, address)
            setUserVoted(voted)
          }

          // Fetch proposals or candidates based on voting type
          if (Number(basicInfo.votingType) === 1) {
            // Proposal-based
            const proposalCount = await contract.getProposalCount(campaignId)
            const proposalData = []

            for (let i = 0; i < Number(proposalCount); i++) {
              const proposal = await contract.getProposal(campaignId, i)
              proposalData.push({
                id: i,
                content: proposal.content,
                voteCount: Number(proposal.voteCount),
              })
            }

            setProposals(proposalData)
          } else {
            // Candidate-based
            const candidateCount = await contract.getCandidateCount(campaignId)
            const candidateData = []

            for (let i = 0; i < Number(candidateCount); i++) {
              const candidate = await contract.getCandidate(campaignId, i)
              candidateData.push({
                id: i,
                name: candidate.name,
                address: candidate.candidateAddress,
                statement: candidate.statement || "No statement provided",
                voteCount: Number(candidate.voteCount),
              })
            }

            setCandidates(candidateData)
          }

          // Combine all data
          const campaignData = {
            id: campaignId,
            votingType: Number(basicInfo.votingType),
            restriction: Number(basicInfo.restriction),
            resultType: Number(basicInfo.resultType),
            creator: basicInfo.creator,
            description: basicInfo.description || "No description provided",
            startTime: Number(timeInfo.startTime) * 1000, // Convert to milliseconds
            endTime: Number(timeInfo.endTime) * 1000, // Convert to milliseconds
            status: Number(timeInfo.status),
            totalVotes: Number(stats.totalVotes),
            itemCount: Number(stats.itemCount),
            registeredVoterCount: Number(stats.registeredVoterCount),
          }

          setCampaignInfo(campaignData)

          // Set initial active tab based on campaign status
          if (isAfterEnd) {
            setActiveTab("results")
          } else if (isDuringVoting && userRegistered && !userVoted) {
            setActiveTab("vote")
          } else if (isBeforeStart && campaignData.restriction !== 0 && !userRegistered) {
            setActiveTab("register")
          }

          toast.success("Campaign loaded successfully", { id: "campaign-loading" })
        }
      } catch (err) {
        console.error("Error fetching campaign details:", err)
        setError("Failed to load campaign details. Please try again.")
        toast.error("Failed to load campaign details", { id: "campaign-loading" })
      } finally {
        setLoading(false)
      }
    }

    fetchCampaignData()
  }, [campaignId, contract, events.campaignsCreated, subgraphLoading, address, isConnected])

  // Handle registration success
  const handleRegistrationSuccess = async () => {
    setUserRegistered(true)

    // Refresh campaign stats
    if (contract && campaignId) {
      const stats = await contract.getCampaignStats(campaignId)
      setCampaignInfo((prev) => ({
        ...prev,
        registeredVoterCount: Number(stats.registeredVoterCount),
      }))
    }

    setActiveTab("overview")
    toast.success("Registration successful! You are now registered for this campaign.")
  }

  // Handle vote success
  const handleVoteSuccess = async () => {
    setUserVoted(true)

    // Refresh campaign stats and vote counts
    if (contract && campaignId) {
      const stats = await contract.getCampaignStats(campaignId)

      setCampaignInfo((prev) => ({
        ...prev,
        totalVotes: Number(stats.totalVotes),
      }))

      // Refresh proposal or candidate vote counts
      if (campaignInfo.votingType === 1) {
        // Proposal-based
        const proposalCount = await contract.getProposalCount(campaignId)
        const proposalData = []

        for (let i = 0; i < Number(proposalCount); i++) {
          const proposal = await contract.getProposal(campaignId, i)
          proposalData.push({
            id: i,
            content: proposal.content,
            voteCount: Number(proposal.voteCount),
          })
        }

        setProposals(proposalData)
      } else {
        // Candidate-based
        const candidateCount = await contract.getCandidateCount(campaignId)
        const candidateData = []

        for (let i = 0; i < Number(candidateCount); i++) {
          const candidate = await contract.getCandidate(campaignId, i)
          candidateData.push({
            id: i,
            name: candidate.name,
            address: candidate.candidateAddress,
            statement: candidate.statement || "No statement provided",
            voteCount: Number(candidate.voteCount),
          })
        }

        setCandidates(candidateData)
      }
    }

    setActiveTab("results")
    toast.success("Vote cast successfully! Your vote has been recorded.")
  }

  // Handle share campaign
  const handleShareCampaign = () => {
    if (navigator.share) {
      navigator
        .share({
          title: campaignInfo?.description || `Campaign #${campaignId}`,
          text: `Check out this voting campaign on PolyTix!`,
          url: window.location.href,
        })
        .then(() => toast.success("Campaign shared successfully"))
        .catch((error) => console.error("Error sharing:", error))
    } else {
      // Fallback for browsers that don't support the Web Share API
      navigator.clipboard
        .writeText(window.location.href)
        .then(() => toast.success("Campaign link copied to clipboard"))
        .catch(() => toast.error("Failed to copy link"))
    }
  }

  // Get status message based on campaign state and user status
  const getStatusMessage = () => {
    if (!campaignInfo) return null

    if (isAfterEnd) {
      return (
        <Alert className="bg-purple-50 dark:bg-purple-950/20 border-purple-200 dark:border-purple-900/50">
          <AlertDescription className="flex items-center">
            üìä This campaign has ended. Results are available below.
          </AlertDescription>
        </Alert>
      )
    }

    if (isBeforeStart) {
      if (campaignInfo.restriction === 0) {
        return (
          <Alert className="bg-blue-50 dark:bg-blue-950/20 border-blue-200 dark:border-blue-900/50">
            <AlertDescription className="flex items-center">
              üó≥Ô∏è This campaign is open to anyone. You can cast your vote once the voting starts.
            </AlertDescription>
          </Alert>
        )
      } else if (!userRegistered) {
        return (
          <Alert className="bg-amber-50 dark:bg-amber-950/20 border-amber-200 dark:border-amber-900/50">
            <AlertDescription className="flex items-center">
              üîê This campaign requires registration. Register now with the passkey to vote once voting starts.
            </AlertDescription>
          </Alert>
        )
      } else {
        return (
          <Alert className="bg-green-50 dark:bg-green-950/20 border-green-200 dark:border-green-900/50">
            <AlertDescription className="flex items-center">
              üéâ You are a registered voter in this campaign. All the best! You can view live results once voting
              starts.
            </AlertDescription>
          </Alert>
        )
      }
    }

    if (isDuringVoting) {
      if (userVoted) {
        return (
          <Alert className="bg-green-50 dark:bg-green-950/20 border-green-200 dark:border-green-900/50">
            <AlertDescription className="flex items-center">
              üôå Thanks for voting using PolyTix. Your vote has been securely recorded.
            </AlertDescription>
          </Alert>
        )
      } else if (campaignInfo.restriction !== 0 && !userRegistered) {
        return (
          <Alert className="bg-amber-50 dark:bg-amber-950/20 border-amber-200 dark:border-amber-900/50">
            <AlertDescription className="flex items-center">
              üçø You were not registered for this campaign. You can still view the results while grabbing some popcorn.
            </AlertDescription>
          </Alert>
        )
      } else {
        return (
          <Alert className="bg-blue-50 dark:bg-blue-950/20 border-blue-200 dark:border-blue-900/50">
            <AlertDescription className="flex items-center">
              üó≥Ô∏è Voting is now open! Cast your vote before the deadline.
            </AlertDescription>
          </Alert>
        )
      }
    }

    return null
  }

  if (loading) {
    return (
      <div className="container max-w-4xl mx-auto py-10 px-4">
        <div className="space-y-6">
          <div className="flex items-center gap-2">
            <Skeleton className="h-6 w-32" />
          </div>
          <Skeleton className="h-12 w-full" />
          <Skeleton className="h-8 w-3/4" />
          <Skeleton className="h-24 w-full" />
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
            <Skeleton className="h-32 w-full" />
          </div>
          <Skeleton className="h-64 w-full" />
        </div>
      </div>
    )
  }

  if (error) {
    return (
      <div className="container max-w-4xl mx-auto py-10 px-4">
        <Alert variant="destructive">
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>{error}</AlertDescription>
        </Alert>
        <Button variant="outline" className="mt-4" onClick={() => router.push("/campaigns")}>
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Campaigns
        </Button>
      </div>
    )
  }

  if (!campaignInfo) {
    return (
      <div className="container max-w-4xl mx-auto py-10 px-4">
        <Alert>
          <AlertCircle className="h-4 w-4" />
          <AlertDescription>Campaign not found or still loading...</AlertDescription>
        </Alert>
        <Button variant="outline" className="mt-4" onClick={() => router.push("/campaigns")}>
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Campaigns
        </Button>
      </div>
    )
  }

  return (
    <div className="container max-w-4xl mx-auto py-10 px-4">
      <Toaster position="top-center" richColors />

      {/* Back button and Share */}
      <div className="flex justify-between items-center mb-6">
        <Link
          href="/campaigns"
          className="inline-flex items-center text-sm text-muted-foreground hover:text-foreground"
        >
          <ArrowLeft className="h-4 w-4 mr-2" />
          Back to Campaigns
        </Link>

        <Button variant="outline" size="sm" onClick={handleShareCampaign} className="gap-1">
          <Share2 className="h-4 w-4" />
          Share
        </Button>
      </div>

      {/* Campaign Title */}
      <div className="text-center mb-8">
        <h1 className="text-3xl font-bold uppercase tracking-tight">{campaignInfo.description}</h1>
        <div className="flex justify-center gap-2 mt-2">
          <Badge variant={isDuringVoting ? "default" : "outline"}>
            {isBeforeStart ? "Upcoming" : isDuringVoting ? "Active" : "Ended"}
          </Badge>
          <Badge variant="outline">{campaignInfo.votingType === 0 ? "Candidate-Based" : "Proposal-Based"}</Badge>
          <Badge variant="outline">
            {campaignInfo.restriction === 0
              ? "Open To All"
              : campaignInfo.restriction === 1
                ? "Limited"
                : "Registration Required"}
          </Badge>
          <Badge variant="outline">{campaignInfo.resultType === 0 ? "Rank Based" : "One Winner"}</Badge>
        </div>
      </div>

      {/* Countdown Timer */}
      <Card className="mb-6 border-2 border-primary/10 shadow-md">
        <CardContent className="pt-6 text-center">
          <h2 className="text-xl font-semibold mb-4">
            {isBeforeStart ? "Voting Starts In:" : isDuringVoting ? "Voting Ends In:" : "Campaign Ended"}
          </h2>
          {!isAfterEnd ? (
            <CampaignCountdown
              endTime={isBeforeStart ? campaignInfo.startTime : campaignInfo.endTime}
              className="max-w-md mx-auto"
            />
          ) : (
            <div className="text-lg font-medium text-muted-foreground">Final results are now available</div>
          )}
        </CardContent>
      </Card>

      {/* Status Message */}
      <div className="mb-6">{getStatusMessage()}</div>

      {/* Campaign Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
        <Card className="hover:shadow-md transition-shadow">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <CalendarClock className="h-5 w-5 text-primary mt-0.5" />
              <div>
                <h3 className="font-medium">Timeline</h3>
                <div className="text-sm text-muted-foreground mt-2">
                  <div>Start: {formatDate(campaignInfo.startTime)}</div>
                  <div>End: {formatDate(campaignInfo.endTime)}</div>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="hover:shadow-md transition-shadow">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <Users className="h-5 w-5 text-primary mt-0.5" />
              <div>
                <h3 className="font-medium">Participation</h3>
                <div className="text-sm text-muted-foreground mt-2">
                  <div>Total Votes: {campaignInfo.totalVotes}</div>
                  {campaignInfo.restriction !== 0 && <div>Registered Voters: {campaignInfo.registeredVoterCount}</div>}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        <Card className="hover:shadow-md transition-shadow">
          <CardContent className="pt-6">
            <div className="flex items-start gap-3">
              <Clock className="h-5 w-5 text-primary mt-0.5" />
              <div>
                <h3 className="font-medium">Status</h3>
                <div className="text-sm text-muted-foreground mt-2">
                  <div>{isBeforeStart ? "Registration Phase" : isDuringVoting ? "Voting Active" : "Voting Ended"}</div>
                  {isConnected ? (
                    <div>
                      {userRegistered ? `Registered: ${userVoted ? "Voted" : "Not voted yet"}` : "Not registered"}
                    </div>
                  ) : (
                    <div>Connect wallet to check status</div>
                  )}
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>

      {/* Tabs */}
      <Tabs value={activeTab} onValueChange={setActiveTab} className="mt-8">
        <TabsList className="grid grid-cols-2 md:grid-cols-4 mb-6 w-full">
          <TabsTrigger value="overview">Overview</TabsTrigger>
          {isBeforeStart && campaignInfo.restriction !== 0 && !userRegistered && (
            <TabsTrigger value="register">Register</TabsTrigger>
          )}
          {isDuringVoting && (campaignInfo.restriction === 0 || userRegistered) && !userVoted && (
            <TabsTrigger value="vote">Vote</TabsTrigger>
          )}
          {(isDuringVoting || isAfterEnd || isOwner) && <TabsTrigger value="results">Results</TabsTrigger>}
        </TabsList>

        <TabsContent value="overview" className="focus-visible:outline-none focus-visible:ring-0">
          <OverviewTab
            campaignInfo={campaignInfo}
            isOwner={isOwner}
            isBeforeStart={isBeforeStart}
            isDuringVoting={isDuringVoting}
            isAfterEnd={isAfterEnd}
            proposals={proposals}
            candidates={candidates}
          />
        </TabsContent>

        {isBeforeStart && campaignInfo.restriction !== 0 && !userRegistered && (
          <TabsContent value="register" className="focus-visible:outline-none focus-visible:ring-0">
            <RegisterTab
              campaignInfo={campaignInfo}
              contract={contract}
              campaignId={campaignId}
              isConnected={isConnected}
              connectWallet={connectWallet}
              onRegistrationSuccess={handleRegistrationSuccess}
            />
          </TabsContent>
        )}

        {isDuringVoting && (campaignInfo.restriction === 0 || userRegistered) && !userVoted && (
          <TabsContent value="vote" className="focus-visible:outline-none focus-visible:ring-0">
            <VoteTab
              campaignInfo={campaignInfo}
              proposals={proposals}
              candidates={candidates}
              contract={contract}
              campaignId={campaignId}
              isConnected={isConnected}
              connectWallet={connectWallet}
              onVoteSuccess={handleVoteSuccess}
            />
          </TabsContent>
        )}

        {(isDuringVoting || isAfterEnd || isOwner) && (
          <TabsContent value="results" className="focus-visible:outline-none focus-visible:ring-0">
            <ResultsTab campaignInfo={campaignInfo} proposals={proposals} candidates={candidates} isOwner={isOwner} />
          </TabsContent>
        )}
      </Tabs>
    </div>
  )
}
